<template>
	<div class="app-content">
		<div class="container">
			<div class="page-title">
				<h1>Категории</h1>
				<div class="actions">
					<button class="btn btn-success" @click="savePage()" :disabled="saveInProgress">
						<span class="fas fa-save"></span>
						Сохранить
					</button>
				</div>
			</div>
			<div class="page-content">
				<div class="progress" v-if="saveInProgress">
					<div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" style="width: 100%"></div>
				</div>
				<div class="alert alert-success" v-if="isSavedSuccess" role="alert">
					Изменения успешно сохранены
				</div>
				<div class="categories-section">
					<div class="input-group">
						<span class="input-group-text">UA</span>
                        <input type="text" v-model="categoryName['ua']" class="form-control">
                        <span class="input-group-text">RU</span>
                        <input type="text" v-model="categoryName['ru']" class="form-control">
                        <span class="input-group-text">EN</span>
                        <input type="text" v-model="categoryName['en']" class="form-control">
						<button class="btn btn-primary" @click="addCategory()" type="button">Добавить категорию</button>
					</div>
					<div class="accordion">
						<div class="accordion-item" v-for="(category, i) in categories" :key="i">
							<h2 class="accordion-header" :id="'c' + i + '-headingOne'">
								<button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" :data-bs-target="'#c' + i + '-collapseOne'" aria-expanded="false">
									{{ category.name.ua }},
                                    {{ category.name.ru }},
                                    {{ category.name.en }}
								</button>
							</h2>
							<div :id="'c' + i + '-collapseOne'" class="accordion-collapse collapse">
								<div class="accordion-body">
                                    <div class="input-group">
                                        <span class="input-group-text">Изображение</span>
                                        <input type="text" v-model="category.imageURL" class="form-control">
                                    </div>
									<div class="input-lang-group">
                                        <div class="input-group">
                                            <span class="input-group-text">UA</span>
                                            <input type="text" v-model="optionName['ua']" class="form-control">
                                            <span class="input-group-text">RU</span>
                                            <input type="text" v-model="optionName['ru']" class="form-control">
                                            <span class="input-group-text">EN</span>
                                            <input type="text" v-model="optionName['en']" class="form-control">
                                            <button class="btn btn-primary" @click="addOption(i)" type="button">Добавить фильтр</button>
                                        </div>
                                    </div>
                                    <div class="accordion">
                                        <div class="accordion-item" v-for="(option, j) in category.list" :key="j">
                                            <h2 class="accordion-header" :id="'co' + j + '-headingOne'">
                                                <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" :data-bs-target="'#co' + j + '-collapseOne'" aria-expanded="false">
                                                    {{ option.name.ua }},
                                                    {{ option.name.ru }},
                                                    {{ option.name.en }}
                                                </button>
                                            </h2>
                                            <div :id="'co' + j + '-collapseOne'" class="accordion-collapse collapse">
                                                <div class="accordion-body">
                                                    <button class="btn btn-outline-danger btn-block" style="margin-bottom: 15px;" @click="delleteOption(i, j)">Удалить опцию</button>
                                                    <div class="input-group">
                                                        <span class="input-group-text">UA</span>
                                                        <input type="text" v-model="filterName['ua']" class="form-control">
                                                        <span class="input-group-text">RU</span>
                                                        <input type="text" v-model="filterName['ru']" class="form-control">
                                                        <span class="input-group-text">EN</span>
                                                        <input type="text" v-model="filterName['en']" class="form-control">
                                                        <button class="btn btn-primary" @click="addFilter(i, j)" type="button">Добавить пункт</button>
                                                    </div>
                                                    <div class="list-group list-group-flush">
                                                        <div class="list-group-item" v-for="(filter, k) in option.list" :key="k">
                                                            <div>{{ filter.name.ua }}, {{ filter.name.ru }}, {{ filter.name.en }}</div>
                                                            <span class="badge bg-danger rounded" @click="delleteFilter(i, j, k)">
                                                                <span class="fas fa-times"></span>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>


<script>

export default {
    data() {
        return {
            categories: [],
            categoryName: {
                ua: '',
                ru: '',
                en: ''
            },
            optionName: {
                ua: '',
                ru: '',
                en: ''
            },
            filterName: {
                ua: '',
                ru: '',
                en: ''
            },
            saveInProgress: false,
            isSavedSuccess: false
        }
    },
    computed: {
        categoriesList() {
            return this.$store.getters['getCategories'];
        }
    },
    watch: {
        categoriesList(value) {
            this.categories = value;
        }
    },
    methods: {
        addCategory() {
            if (!!this.categoryName.ua) {
                let categoryId = null;
                if (!!this.categories.length) {
                    let prevCatId = parseFloat(this.categories[this.categories.length - 1].id) + 1;
                    categoryId = ((prevCatId < 10) ? ('0' + prevCatId) : prevCatId).toString();
                } else {
                    categoryId = '00'
                }
                this.categories.push({
                    id: categoryId,
                    name: this.categoryName,
                    imageURL: '',
                    list: []
                })
                this.categoryName = '';
            }
        },
        addOption(categoryIndex) {
            if (!!this.optionName) {
                var optionsList = this.categories[categoryIndex].list;
                var optionId = 0;
                
                if (!!optionsList.length) {
                    optionsList.forEach((el) => {
                        var currentId = parseInt(el.id.slice(2));
                        if (currentId > optionId) optionId = currentId;
                    });
                    optionId++;
                    if (optionId < 10) optionId = '0' + optionId;
                } else {
                    optionId = '00';
                }
                optionsList.push({
                    id: this.categories[categoryIndex].id + optionId,
                    name: this.optionName,
                    list: []
                });
                this.optionName = {
                    ua: '',
                    ru: '',
                    en: ''
                };
            }
        },
        addFilter(categoryIndex, optionIndex) {
            console.log(this.categories[categoryIndex].list[optionIndex]);
            if (!!this.filterName) {
                var filtersList = this.categories[categoryIndex].list[optionIndex].list;
                var filterId = 0;
                
                if (!!filtersList.length) {
                    filtersList.forEach((el) => {
                        var currentId = parseInt(el.id.slice(4));
                        if (currentId > filterId) filterId = currentId;
                    });
                    filterId++;
                    if (filterId < 10) filterId = '0' + filterId;
                } else {
                    filterId = '00';
                }
                filtersList.push({
                    id: this.categories[categoryIndex].list[optionIndex].id + filterId,
                    name: this.filterName
                });
                this.filterName = {
                    ua: '',
                    ru: '',
                    en: ''
                };
            }
        },
        delleteOption(categoryIndex, optionIndex) {
            this.categories[categoryIndex].list.splice(optionIndex, 1);	
        },
        delleteFilter(categoryIndex, optionIndex, filterIndex) {
            this.categories[categoryIndex].list[optionIndex].list.splice(filterIndex, 1);	
        },
        savePage() {
            if (!!this.categories.length) {
                this.saveInProgress = true;
                this.$store.dispatch('saveCategories', this.categories)
                    .then(() => {
                        this.saveInProgress = false;
                        this.isSavedSuccess = true;
                        setTimeout(() => {
                            this.isSavedSuccess = false;
                        }, 3000);
                    });
            }
        }
    },
    created() {
        this.categories = this.categoriesList;
    }
}
</script>
